:root {
    --primary-font: "DM Sans", sans-serif;
    --special-font: "Titan One", serif;

    --accent-color: #D41443;
    --second-accent-color: #FFA5BA;
    --primary-text-color: #000000;
    --secondary-text-color: #907E82;
    --clear-white: #FFFFFF;
    --accent-btn-hover: #C7133E;
    --btn-hover: #ECEBEB;

    --menu-bg: #E17992;
    --hero-bg: #FFB8CA;
    --primary-bg: #FFF5F6;
    --pink-bg: #FFB8CA;
    --coffee-bg: #F0D1A5;
    --green-bg: #C2E297;
    --quotes-bg: #FAFAFA;
    --quotes-btn-bg: #D9D9D9;

    --mobile-menu-border: #E18298;
    --primary-border: #E1E1E1;

    --contacts-shadow: #D414431A;

    --transition: ease-in 200ms;

    --mobile-max-width: 320px;
    --tablet-max-width: 768px;
    --desktop-max-width: 1200px;
}

$tablet-break-point: 768px;
$desktop-break-point: 1200px;